
import React, { PureComponent } from 'react'

export default class _app extends PureComponent {
  constructor(props) {
    super(props)

    this.state = {
      tictoc: undefined
    }
  }

setKey = (e) => {
  console.log(e.target.id)
}

  render() {
    return (
      <div id='main' className='body'>
        <div className='mainBox'>
          <div className='rowBox'>
            <div
              id='u1' className='lu sepBox'
              onClick={(e) => this.setKey(e)}></div>
            <div
              id='u2' className=' sepBox'
              onClick={(e) => this.setKey(e)}></div>
            <div
              id='u3' className='ru sepBox'
              onClick={(e) => this.setKey(e)}></div>
          </div>
          <div className='rowBox'>
            <div
              id='m1' className=' sepBox'
              onClick={(e) => this.setKey(e)}></div>
            <div
              id='m2' className='m sepBox'
              onClick={(e) => this.setKey(e)}></div>
            <div
              id='m3' className='sepBox'
              onClick={(e) => this.setKey(e)}></div>
          </div>
          <div className='rowBox'>
            <div
              id='l1' className='ll sepBox'
              onClick={(e) => this.setKey(e)}></div>
            <div
              id='l2' className=' sepBox'
              onClick={(e) => this.setKey(e)}></div>
            <div
              id='l3' className='rl sepBox'
              onClick={(e) => this.setKey(e)}></div>
          </div>
        </div>

        <style jsx>
          {`
              .body {
                background-color: black;
                width: 100vh;
                height: 100vh;
                display: flex;
                justify-content: center;
                align-items: center;
              }
              .mainBox {
                height: 600px;
                width: 600px;
                cursor: pointer;
                }
              .lu{
                border-color: white;
                border-style: solid;
                border-width: 0px 1px 1px 0px;
              }
              .ru{
                border-color: white;
                border-style: solid;
                border-width: 0px 0px 1px 1px;
              }
              .m{
                border-color: white;
                border-style: solid;
                border-width: 1px;
              }
              .ll{
                border-color: white;
                border-style: solid;
                border-width: 1px 1px 0px 0px;
              }
              .rl{
                border-color: white;
                border-style: solid;
                border-width: 1px 0px 0px 1px;
              }
              .rowBox{
                height: 33.33%;
                display: flex;
              }
              .sepBox{
                width: 33.33%;
              }
          `}
        </style>
      </div>
    )
  }
}
